import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter
} from '@angular/core';

@Component({
  selector: 'rate',
  templateUrl: './rate.component.html',
  styleUrls: [
    './rate.component.css',
    './rate.component.css'
  ]
})
export class RateComponent implements OnInit {
  // 
  rateVal: number = 0;
  //  mouseover  rateVal
  rateMemo: number = 0;
  // label 
  rateText: string[] | number[] = ['', '', '', '', ''];
  //  label
  showLabel = false;
  isReadonly = false;

  /**
   *   rate
   */
  @Output() rateChange = new EventEmitter();

  @Input() get rate() {
    let val = this.rateVal;
    val = val < 0 ? 0 : (val > 5 ? 5 : val);
    return val;
  }

  set rate(val: number) {
    this.rateVal = val;
    this.rateChange.emit(this.rateVal);
  }

  /**
   *  text
   */
  @Input() get text() {
    return this.rateText;
  };

  set text(text) {
    this.showLabel = text !== void 0 && text !== null;
    this.rateText = !this.showLabel ? null : (text || this.rateText);
  }

  /**
   * readonly
   */
  @Input() get readonly() {
    return this.isReadonly;
  }

  set readonly(readonly) {
    this.isReadonly = readonly !== void 0 && String(readonly) !== 'false';
  }

  click(rate: number, e: MouseEvent) {
    e.preventDefault();
    this.rate = rate;
    this.rateMemo = this.rate;
  }

  mouseenter(i: number) {
    this.rateMemo = this.rate;
    this.rateVal = i;
  }

  mouseleave(i: number) {
    this.rate = this.rateMemo;
  }

  constructor() {}

  ngOnInit() {}
}

